<template>
    <div class="PhotosView">
        <main class="PhotosView__main">
            <div
              class="PhotosView__image-container"
              :key="photo.id"
              :src="photo"
              v-for="photo in photos"
            >
                <img
                  class="PhotosView__image"
                  :src="photo.photo"
                >
                <Button @onClick="removePhoto(photo.id)" red>Dzēst</Button>
            </div>
            <div>
                <input type="file" accept="image/*" @change="savePhoto" multiple>
            </div>    
        </main>

        <div class="PhotosView__actions">
            <Button red @onClick="goBack">ATPAKAĻ</Button>
        </div>
    </div>
</template>

<script>
import Button from '../components/Button.vue';

export default {
    name: 'PhotoView',

    components: { Button },

    created() {
        this.$store.dispatch('loadPhotos')
    },

    computed: {
        photos() {
            return this.$store.getters.project.photos;
        }
    },

    methods: {
        savePhoto(event) {
            this.$store.dispatch('savePhotos', event.target.files)
        },
        removePhoto(id) {
            this.$store.dispatch('deletePhoto', id)
        },
        goBack() {
            this.$router.go(-1);
        }
    }
}
</script>

